'use client';

import InterviewCard from './InterviewCard';

interface Interview {
  id: string;
  role: string;
  type: string;
  techstack: string[];
  createdAt: string;
}

export default function InterviewCardClient({ interview, userId }: { interview: Interview; userId: string }) {
  return (
    <InterviewCard
      interviewId={interview.id}
      userId={userId}
      role={interview.role}
      type={interview.type}
      techstack={interview.techstack}
      createdAt={interview.createdAt}
    />
  );
}
